<template>
    <div class="coupon-layer" v-if="couponFlag">
        <div class="coupon">
            <h2 class="coupon-tit">优惠</h2>
            <div class="coupon-wrap">
                <h3 class="coupon-sign">促销</h3>
                <div class="coupon-integral">
                    <span>积分</span>
                    <span>购买可得17积分</span>
                </div>
            </div>
            <van-popup v-model="show" position="top" :overlay="false">领取成功</van-popup>
            <div class="coupon-wrap">
                <div class="coupon-sign">
                    <span>领券</span>
                    <span class="existing-integral">已有积分: <span>590</span></span>
                </div>
                <ul class="coupon-list">
                    <li class="coupon-item" :style="couponBg" v-for="(c,index) in coupons" :key="c.couponId">
                        <span class="coupon-money" v-text="c.couponMoney"></span>
                        <div class="coupon-range">
                            <span v-text="c.couponRange"></span>
                            <a href="javascript:;" class="coupon-btn" @click="receive(index)"></a>
                        </div>
                    </li>
                </ul>
            </div>
            <div class="coupon-confirm">
                <a class="confirm-btn" href="javascript:;" @click="closeCoupon">完成</a>
            </div>
        </div>
    </div>    
</template>

<script>
import { mapMutations } from 'vuex'
export default {
    name: 'CouponLayer',
    data() {
        return {
            coupons: [
                { couponId: 'c01', couponMoney: 5, couponRange: '无门槛使用' },
                { couponId: 'c02', couponMoney: 10, couponRange: '满50使用' },
                { couponId: 'c03', couponMoney: 20, couponRange: '满100使用' }
            ],
            show: false
        }
    },
    props: {
        couponFlag: {
            type: Boolean,
            required: true
        }
    },
    computed: {
        couponBg() {
            return {
                background: `url(${ require('~static/images/shop_coupons.png') }) no-repeat`,
                backgroundSize: '100% 100%'
            }
        }
    },
    methods: {
        ...mapMutations(['receiveCoupon']),
        receive(idx) {
            let coupon = {
                number: 1,
                serviceLife: '2019-10-20',
                ...this.coupons[idx]
            };
            this.receiveCoupon(coupon);
            this.popup();
        },
        closeCoupon() {
            this.$emit('coupon-close',false);
        },
        popup() {   //提示信息弹层
            this.show = true;
            setTimeout(() => {
                this.show = false;
            },500);
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin';
    .coupon-layer
        max_width() 
        van_popup()
        full_screen(5,fixed,0,0,0,0,rgba(0,0,0,.6))
        .coupon
            max_width() 
            full_screen(6,fixed,20%,0,0,0,#fff)
            border-radius .2rem .2rem 0 0
            padding 0 .3rem
            box-sizing border-box
            &-tit
                font-size .34rem
                margin-top .4rem
                margin-bottom .3rem
            &-wrap
                text-align left
                font-size .26rem
                ^[-1]-sign
                    color $gray
                    font-size .3rem
                    margin-bottom .2rem
                    .existing-integral
                        float right
                        font-size .26rem
                ^[-1]-integral
                    margin .3rem 0 .5rem .2rem
                    > span
                        font-size .26rem
                        &:first-child
                            color $red
                            margin-right .1rem
                ^[-1]-list
                    lateral_drag(6.9rem,hidden,auto)
                    height 5.5rem
                    margin .5rem auto 0
                    ^[-2]-item
                        float left
                        width 3.3rem
                        height 0
                        padding-bottom 25.64%
                        margin-bottom .3rem
                        position relative 
                        &:nth-child(2n-1)
                            margin-right .3rem
                        ^[-3]-money
                            color $white
                            position absolute
                            top .2rem
                            left .8rem
                            font-size .5rem
                        ^[-3]-range
                            color $red
                            position absolute
                            bottom .3rem
                            left .5rem
                            font-size .22rem
                            ^[-4]-btn
                                position absolute
                                top -.08rem
                                left 1.2rem
                                width 1.25rem
                                height .4rem
            &-confirm
                position absolute
                bottom 0
                width 92%
                height .8rem
                border-radius .5rem
                background $red
                .confirm-btn
                    vertical(.8rem)
                    display block
                    color $white
                    font-size .32rem
</style>



