<template>
    <div class="detail-box">
        <detail-swiper :commodityItem="commodityItem" type="home"></detail-swiper>
        <detail-content :commodityItem="commodityItem" @coupon-show="setCouponLayer"></detail-content>
        <shopping-cart-nav @show="setLayerShow"></shopping-cart-nav>
        <mask-layer :commodityItem="commodityItem" :layerFlag="layerFlag" @close="setLayerShow"></mask-layer>
        <coupon-layer :couponFlag="couponFlag" @coupon-close="setCouponLayer"></coupon-layer>
    </div>    
</template>

<script>
import { request } from '@/assets/javascript/request.js'
import DetailSwiper from './components/DetailSwiper'
import DetailContent from './components/DetailContent'
import ShoppingCartNav from '@/common/ShoppingCartNav'
import MaskLayer from './components/MaskLayer'
import CouponLayer from './components/CouponLayer'
export default {
    name: "HomeCommodityDetail",
    data() {
        return {
            commodity: [],
            layerFlag: false,
            couponFlag: false
        }
    },
    components: {
        DetailSwiper,
        DetailContent,
        ShoppingCartNav,
        MaskLayer,
        CouponLayer
    },
    computed: {
        commodityType() {   //获取商品进入的区域
            return this.$route.params.type;
        },
        goodsId() {     //获取商品id
            return this.$route.params.id;
        },
        commodityItem() {   //获取商品id对应的项
            let result = this.commodity.filter((item) => item.goodsId == this.goodsId);
            return result[0];
        }
    },
    methods: {
        init() {
            this.$axios.get(request.url.home).then(this.getData);
        },
        getData(res) {
            if(res.status == 200 && res.data.data) {
                res = res.data.data;
                switch(this.commodityType) {
                    case 'slides':
                        this.commodity = res.slides;
                        break;
                    case 'hotGoods':
                        this.commodity = res.hotGoods;
                        break;
                    case 'floor1':
                        this.commodity = res.floor1;
                        break;
                    case 'floor2':
                        this.commodity = res.floor2;
                        break;
                    case 'floor3':
                        this.commodity = res.floor3;
                        break;
                }
            }
        },
        setLayerShow(flag) {
            this.layerFlag = flag;
        },
        setCouponLayer(flag) {
            this.couponFlag = flag;
        }
    },
    created() {
        this.init();
    }
}
</script>

<style lang="stylus" scoped>
    .detail-box
        padding-bottom 1rem
</style>



