<template>
    <div class="all-coupon">
        <ul class="all-coupon-list" v-if="couponsFlag">
            <li class="all-coupon-item" :style="couponSty" v-for="c in coupons" :key="c.couponId">
                <div class="all-coupon-money">
                    &#165;<span v-text="c.couponMoney"></span>
                </div>
                <div class="all-coupon-desc">
                    <p>全场商品</p>
                    <p v-text="c.couponRange"></p>
                </div>
                <div class="all-coupon-desc number">数量：<span v-text="c.number"></span></div>
                <div class="all-coupon-desc date"><span v-text="c.serviceLife"></span> 截至到期</div>
                <div class="all-coupon-use">
                    <a href="javascript:;" v-if="flag == '未使用'">立即使用&#10148;</a>
                    <a href="javascript:;" v-else-if="flag == '已使用'">已使用</a>
                    <a href="javascript:;" v-else-if="flag == '已过期'">已过期</a>
                </div>
            </li>
        </ul>
        <div v-else class="no-coupon" :style="noCouponSty" >
            <p>暂无<span v-text="flag"></span>的优惠券</p>
        </div>
    </div>
</template>

<script>
export default {
    name: 'AllCoupon',
    props: {
        coupons: {
            type: Array,
            required: true
        },
        flag: {
            type: String,
            required: true
        }
    },
    computed: {
        couponSty() {
            return {
                background: `url(${ require('~static/images/custom-coupon.png') }) no-repeat`,
                backgroundSize: '100% 100%'
            }
        },
        noCouponSty() {
            return {
                background: `url(${ require('~static/images/no-coupon.png') }) no-repeat`,
                backgroundSize: '100% 100%'
            }
        },
        couponsFlag() {
            return this.coupons.length;
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .all-coupon
        positive_center(fixed)
        margin-top 1.7rem
        padding-bottom .3rem
        ^[0]-list
            lateral_drag(6.5rem,hidden,auto)
            height 100%
            margin 0 auto
            ^[0]-item
                color #F6F6FB
                width 100%
                height 0
                margin .3rem auto 0
                padding-bottom 48%
                overflow hidden
                position relative
                ^[0]-money
                    font-size .5rem
                    position absolute
                    top .8rem
                    right .8rem
                    >span
                        font-size 1.6rem
                        margin-left .1rem
                ^[0]-desc
                    font-size .3rem
                    position absolute
                    top 1.4rem
                    left .5rem
                    >p
                        margin-bottom .1rem
                    &.number
                        top 2.6rem
                        > span
                            font-size .36rem
                    &.date
                        top .42rem
                        left 3.6rem
                        font-size .22rem
                ^[0]-use
                    position absolute
                    right .3rem
                    bottom .2rem
                    >a
                        display block
                        color $white
                        font-size .28rem
                        border-radius .4rem
        .no-coupon
            positive_center(absolute)
            width 60%
            height 0
            padding-bottom 70%
            >p
                color $darkGray
                margin-top 5.7rem
                font-size .3rem
</style>




