<template>
    <div class="coupon">
        <van-nav-bar
            title="我的优惠券"
            right-text="优惠券说明"
            left-arrow
            @click-left="back"
        ></van-nav-bar>
        <ul class="coupon-nav">
            <li 
                v-for="(item,index) in navs" 
                :key="index" 
                :class="['coupon-nav-item',  currentIndex == index && 'active']" 
                @click="getCoupon(index)" 
                v-text="item"
            ></li>
        </ul>
        <all-coupon :coupons="coupons" :flag="flag"></all-coupon>
    </div>    
</template>

<script>
import { mapState } from 'vuex'
import AllCoupon from './coupon/AllCoupon'
export default {
    name: 'Coupon',
    data() {
        return {
            currentIndex: 0,
            navs: ['未使用','已使用','已过期'],
            coupons: [],
            flag: '未使用'
        }
    },
    components: {
        AllCoupon
    },
    computed: {
        ...mapState(['notUsed','alreadyUsed','expired'])
    },
    methods: {
        back() {
            this.$router.go(-1);
        },
        getCoupon(idx) {
            this.currentIndex = idx;
             switch(idx) {
                case 0:
                    this.coupons = this.notUsed;
                    this.flag = '未使用';
                    break;
                case 1:
                    this.coupons = this.alreadyUsed;
                    this.flag = '已使用';
                    break;
                case 2:
                    this.coupons = this.expired;
                    this.flag = '已过期';
                    break;
            }
        }
    },
    activated() {
        this.coupons = this.notUsed;
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .coupon
        van_nav()
        &-nav
            vertical(.8rem)
            background-color $white
            overflow hidden
            &-item
                float left
                width (100/3)%
                font-size .28rem
                position relative
                &.active
                    color $violet
                    &:after
                        content ''
                        position absolute
                        right 0
                        bottom 0
                        left 0
                        width 50%
                        height .06rem
                        background-color $violet
                        margin 0 auto
</style>



