<template>
    <div class="process">
        <div class="commodity-detail-head">
            <div class="commodity-detail-head-wrap" @click="back">
                <span class="iconfont head-icon">&#xe669;</span>
            </div>
        </div>
        <div class="process-wrap"><img :src="process" alt="购物流程"></div>
    </div>    
</template>

<script>
export default {
    name: 'ShoppingProcess',
    data() {
        return {
           process: require('~static/images/shopping_process.png') 
        }
    },
    methods: {
        back() {
            this.$router.go(-1);
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .process
        detail_top()
        &-wrap
            positive_center(absolute)
            >img
                width 100%
</style>



