<template>
    <div class="receiving-address">
        <van-nav-bar
            title="我的收货地址"
            right-text="添加新地址"
            left-arrow
            @click-left="back"
            @click-right="forward"
        ></van-nav-bar>
        <div class="no-addr" v-if="!length">
            <span class="iconfont icon-addr">&#xe662;</span>
            <p class="no-addr-message">暂无收货地址，请重新添加</p>
        </div>
        <div class="address-wrap">
            <van-cell-group v-if="length">
                <van-cell v-for="item in addressArr" :key="item.id">
                    <template slot="title">
                        <div class="head-portrait" v-text="item.contacts"></div>
                        <div class="user-info">
                            <P class="user-wrap">
                                <span v-text="item.contacts"></span>
                                <span class="user-tel" v-text="item.telephone"></span>
                            </P>
                            <P class="user-wrap user-alone">
                                <span class="default-icon" v-if="item.checked">默认</span>
                                <span class="my-addr" :style="item.checked && defaultSty" v-text="`${item.region} ${item.address}`"></span>
                            </P>
                        </div>
                    </template>
                    <router-link to="/addr_modify/edit" slot="right-icon" class="edit-btn border-left" @click.native="setIndex(item.id)">编辑</router-link>
                </van-cell>
            </van-cell-group>
        </div>
    </div>
</template>

<script>
import { mapState,mapMutations } from 'vuex'
export default {
    name: 'AddressManage',
    data() {
        return {
            defaultSty: {
                textIndent: '.8rem'
            }
        }
    },
    computed: {
        ...mapState(['addressArr','backRoute']),
        length() {
            return this.addressArr.length;
        },
        flag() {
            return this.$route.params.flag;
        }
    },
    methods: {
        ...mapMutations(['setAddrIndex']),
        back() {
            this.$router.replace(this.backRoute);
        },
        forward() {
            this.$router.push('/addr_modify/add');
        },
        setIndex(idx) {
            this.setAddrIndex(idx);
        }
    }

}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .receiving-address
        van_nav()
        .no-addr
            color #ccc
            margin-top 40%
            .icon-addr
                font-size 2rem
            &-message
                margin-top .4rem
                font-size .3rem
        & >>> .van-nav-bar
            background-color $white
        & >>> .address-wrap
            positive_center(fixed)
            margin-top .9rem
            .van-cell-group
                lateral_drag(100%,hidden,auto)
                height 100%
                .van-cell
                    padding .3rem
                    line-height .3rem
                    position relative
                    .head-portrait
                        vertical(.6rem)
                        float left
                        color $white
                        width .6rem
                        font-size .24rem
                        border-radius 50%
                        background-color $gray
                        margin-top .1rem
                    .user-info
                        float left
                        text-align left 
                        font-size .32rem
                        margin-left .2rem
                        .user-wrap
                            padding 0
                            .user-tel
                                color $gray
                                font-size .26rem
                                margin-left .1rem
                            &.user-alone
                                multiple_rows(5rem,2)
                                font-size .28rem
                                line-height .34rem
                                margin-top .1rem
                                padding-top .04rem
                                position relative
                                .default-icon
                                    position absolute
                                    top 0
                                    left 0
                                    color $red
                                    font-size .22rem
                                    padding 0 .08rem
                                    background-color rgba(254,0,0,.2)
                                .my-addr
                                    font-size .26rem
                                    display block
                    .edit-btn
                        vertical(.4rem)
                        color $darkGray
                        font-size .28rem
                        padding-left .3rem
                        margin-top .15rem
</style>



